.header__outer {
  z-index: 2;
  position: fixed;
  top: 0;
  left: 0;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  height: 40px;
  width: 100vw;
  padding: 0 80px;
  background-image: -webkit-gradient(linear, left top, left bottom, from(#afa5b9), to(rgba(255, 255, 255, 0.8)));
  background-image: -webkit-linear-gradient(#afa5b9, rgba(255, 255, 255, 0.8));
  background-image: -o-linear-gradient(#afa5b9, rgba(255, 255, 255, 0.8));
  background-image: linear-gradient(#afa5b9, rgba(255, 255, 255, 0.8)); }

.header__inner {
  min-width: 250px;
  text-align: center; }

.header__flags {
  margin: 0 10px;
  opacity: 0.3;
  cursor: pointer; }

.header__flags--active {
  opacity: 1;
  cursor: default; }

.header__text-contact {
  font-family: "Corben", cursive;
  font-weight: bold; }



